@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body{
  width:100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
}

.quiz-app{
  width: 600px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.question-section{
  text-align: center;
}

.question-section h2{
  margin: 10px 0px;
  font-weight: 500;
  color: #3498;
}

.question-section p{
  font-weight: 500;
}

.options{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}

button{
  padding: 10px;
  font-size: 16px;
  border-radius: 30px;
  border: none;
  background-color: darkblue ;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s; 
  }

  button:hover{
    filter: brightness(1.2);
  }

  .timer{
    margin-top: 20px;
    font-size: 18px;
    color: #555;
  }

  .timer span{
    font-weight: 800px;
  }

  .score-section{
    text-align: center;
  }

  .score-section h2 {
    font-weight: 500;
    margin-bottom: 15px;
  }

  .score-section button{
    background-color: tomato;
  }